.header {
  padding: 16px 15px;
  .logo {
    @include response(767px) {
      max-width: 85px;
    }
  }
  &__inner {
    max-width: 1210px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 25px;
  }
  .search {
    &__handler {
      padding: 0;
      background: transparent;
      border: none;
      cursor: pointer;
    }
  }
  &__cart-btn {
    cursor: pointer;
    position: relative;
    &-marker {
      position: absolute;
      right: -15px;
      top: -14px;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      font-size: 11px;
      padding-top: 2px;
      font-weight: 500;
      background: #FF5981;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  @include response(767px) {
    padding: 10px 15px;
  }
}

.navigation {
  &__list {
    display: flex;
    align-items: center;
    gap: 40px;
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: 14px;
    @include response(1100px) {
      gap: 20px;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 8px;
    text-decoration: none;
    color: #1a0a1b;
    svg path {
      @include transition();
    }
    @include transition();
    &:hover {
      color: #FF5981;
      svg path {
        stroke: #FF5981;
      }
    }
  }
  &__item:last-child .navigation__link {
    color: #FF5981;
    font-weight: 500;
  }
  .products-btn {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 500;
    color: #FF5981;
    cursor: pointer;
  }
  @include response(992px) {
    display: none;
  }
}

.notification {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  height: 35px;
  font-size: 13px;
  background: #1a0a1b;
  color: #fff;
  @include response(767px) {
    font-size: 11px;
  }
}

.burger {
  width: 36px;
  height: 36px;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: linear-gradient(135deg, #FFE3D1 0%, #F0D4FF 100%);
  display: none;
  @include response(992px) {
    display: flex;
  }
}
